.about {
  height: 100vh;
  width: 75vw;
  background-color: grey;
  align-self: center;
  margin-bottom:10px;
}

#about {
  display: flex;
}

#me {
  width: 30%;
  height: 30%;
}

#name {
  color: white;
  font-size: 40px;
  display: flex;
  justify-content: center;
  width: 70%;
  align-items: center;
  background-color: rgb(90, 82, 82);
}

#life {
  width: 100%;
  background-color: grey;
  color: white;
}

#skills {
  width: 100%;
  background-color: grey;
  color: white;
}

@media only screen and (max-width: 600px) {
 
  .about{
    width:90%;
  }
}
